<template>
  <div class="post-wrap">
    <!-- Tabs Nav -->
    <ul class="tab-nav-wrap" data-panel="psn-tabs">
        <li class="tab-nav active" data-tabs="section1">遊戲介紹</li>
        <li class="tab-nav" data-tabs="section2">遊戲畫面</li>
    </ul>

    <!-- Tabs Content -->
    <div class="tab-content-wrap fade-tab" data-panel="psn-tabs">
        <!-- Tab 1 start -->
        <div class="tab-content active" data-tabs="section1">
            <h3>使用滑鼠操作</h3>
            <ul>
                <li>拾起物品：點擊畫面中可拾起的物品，會放至下方工具列</li>
                <li>放置物品：從工具列拖曳物品到畫面中</li>
            </ul>

            <h3>遊戲關卡類型</h3>
            <p>遊戲觸發事件總共分為三種類型，分別為「偵測型」、「密碼型」及「放置型」。</p>

            <h4>偵測型事件</h4>
            <p>工具列中需有特定物品，才可開啟隱藏畫面。</p>

            <h6>▼ 沒有道具「燈」</h6>
            <div class="img-wrap">
                <img src="@/assets/images/work/escape/escape_room_game_8.png" />
            </div>

            <h6>▼ 有道具「燈」，可進入左右通道</h6>
            <div class="img-wrap">
                <img src="@/assets/images/work/escape/escape_room_game_9.png" />
            </div>

            <h4>密碼型事件</h4>
            <p>需輸入正確的數字，才可開啟隱藏畫面。</p>
            <h6>▼ 輸入正確「份量」</h6>
            <div class="img-wrap">
                <img src="@/assets/images/work/escape/escape_room_game_10.png" />
            </div>

            <h6>▼ 進入隱藏畫面</h6>
            <div class="img-wrap">
                <img src="@/assets/images/work/escape/escape_room_game_11.png" />
            </div>

            <h4>放置型事件</h4>
            <p>須將正確的物品拖曳至正確的畫面位置中，才可觸發特定事件。</p>
            <h6>▼ 給小老鼠喝「湯」</h6>
            <div class="img-wrap">
                <img src="@/assets/images/work/escape/escape_room_game_12.png" />
            </div>

            <h6>▼ 獲得「小老鼠」道具</h6>
            <div class="img-wrap">
                <img src="@/assets/images/work/escape/escape_room_game_13.png" />
            </div>
        </div>
        <!-- Tab 1 end -->
        
        <!-- Tab 2 start -->
        <div class="tab-content" data-tabs="section2">
            <h3>遊戲畫面部分截圖</h3>
            <div class="img-wrap">
                <img src="@/assets/images/work/escape/escape_room_game_3.png" />
            </div>

            <div class="img-wrap">
                <img src="@/assets/images/work/escape/escape_room_game_4.png" />
            </div>

            <div class="img-wrap">
                <img src="@/assets/images/work/escape/escape_room_game_5.png" />
            </div>

            <div class="img-wrap">
                <img src="@/assets/images/work/escape/escape_room_game_6.png" />
            </div>
        </div>
        <!-- Tab 2 end -->
    </div>
  </div>
</template>

<script>
// import jquery
import $ from "jquery";
window.$ = $;

export default {
    name: 'SpEscapeRoomGame',
    mounted() {
      /* if not mac, hide scroll */
      this.isMac = navigator.platform.toUpperCase().indexOf('MAC') >= 0; // not found = -1
      let $dev_outter_wrap = $('.dev-outter-wrap');
      if ( !this.isMac && $dev_outter_wrap.length > 0 ) {
        $dev_outter_wrap.addClass('hide-scroll');
      }

      /* tabs feature */
      $('.tab-nav').on('click', function(){
        let tab_panel = $(this).parent('.tab-nav-wrap').attr('data-panel');
        
        let tab = $(this).attr('data-tabs');
        $(this).siblings('.active').removeClass('active');
        $(this).addClass('active');
    
        let $tab_content_wrap = $('.tab-content-wrap').filter(`[data-panel="${tab_panel}"]`);
        $tab_content_wrap.find('.active').removeClass('active');
        $tab_content_wrap.find(`[data-tabs="${tab}"]`).addClass('active');

        $('html, body').animate({
            'scrollTop': $(this).offset().top - 70
        }, 300);
      });
    }
}
</script>

